import { cartSignal } from '@/checkout/signals';
import { CartTaxItem } from '@/checkout/__generated__/types';
import {
  ConfigurableOption,
  Order,
  ShoppingCart,
  ShoppingCartItem,
} from '@/checkout/types/CompleteCheckoutContextSchemas';
import { Cart } from '@/checkout/data/models/cart';
import { Item, ItemKind } from '@/checkout/data/models/cart-item';
import { ShippingMethod } from '@/checkout/data/models';

function mapConfigurableOptions(
  options: Record<string, string>
): ConfigurableOption[] | undefined {
  const result: ConfigurableOption[] = [];

  for (const key in options) {
    if (options[key] === null) continue;

    result.push({
      optionLabel: key,
      valueLabel: options[key],
    });
  }

  return result;
}

function mapCartItems(items: Item[]): ShoppingCartItem[] {
  return items.map((item: Item): ShoppingCartItem => {
    const { uid, name, sku, price, quantity } = item;

    let cartItem: ShoppingCartItem = {
      /* We don't know if Checkout drop-in can apply Manufacturerâ€™s Suggested Retail Price */
      canApplyMsrp: true,
      formattedPrice: '',
      id: uid,
      prices: {
        price: {
          value: price.value ?? 0,
          currency: price.currency ?? '',
        },
      },
      product: {
        /* productId not exposed by core GraphQL as number (uid is string) */
        productId: 0,
        name,
        sku,
        productType: item.kind,
        canonicalUrl: item.url || '',
        mainImageUrl: item?.image?.src || '',
      },
      quantity,
    };

    if (item.kind === ItemKind.Configurable) {
      cartItem.configurableOptions =
        // @ts-ignore
        mapConfigurableOptions(item.configurableOptions);
    }

    return cartItem;
  }, []);
}

/**
 * This function builds the ShoppingCart context.
 * It maps the shopping cart data collected during the checkout process to the
 * specific schema format defined by the Adobe Commerce Events SDK.
 *
 * @param {Cart} cart The cart data collected during the checkout process.
 * @returns {ShoppingCart} The ShoppingCart data formated.
 */
function buildShoppingCartContext(cart: Cart): ShoppingCart {
  const { id, items, prices, totalQty } = cart;
  const hasItems = !!items?.length;

  let shoppingCartContext: ShoppingCart = {
    id,
    prices: {
      subtotalExcludingTax: {
        value: prices?.subtotal_excluding_tax?.value ?? 0,
        currency: prices?.subtotal_excluding_tax?.currency ?? '',
      },
      subtotalIncludingTax: {
        value: prices?.subtotal_including_tax?.value ?? 0,
        currency: prices?.subtotal_including_tax?.currency ?? '',
      },
    },
    totalQuantity: totalQty,
    possibleOnepageCheckout: true,
  };

  if (hasItems) {
    shoppingCartContext.items = mapCartItems(items);
  }

  return shoppingCartContext;
}

function calculateTaxes(taxes: (CartTaxItem | null)[]): number {
  const totalTaxAmount = taxes.reduce(
    (total, tax) => total + tax!.amount.value!,
    0
  );

  return totalTaxAmount;
}

function buildShippingMethod(method: ShippingMethod) {
  return {
    shippingMethod: `${method.carrier.code}_${method.code}`,
    shippingAmount: method.amount.value ?? 0,
  };
}

/**
 * This function builds the Order context.
 * It maps the order data collected during the checkout process to the specific
 * schema format defined by the Adobe Commerce Events SDK.
 *
 * @param {string} orderId The order ID generated when placed the order.
 * @param {Cart} cart The cart data collected during the checkout process.
 * @returns {Order} The Order data formated.
 */
function buildOrderContext(orderId: string, cart: Cart): Order {
  const { coupons, email, prices, selectedPaymentMethod, shippingAddresses } =
    cart;
  const hasAddresses = !!shippingAddresses?.length;

  let orderContext: Order = {
    appliedCouponCode: coupons[0]?.code ?? '',
    email: email ?? '',
    grandTotal: prices?.grand_total?.value ?? 0,
    orderId,
    orderType: 'checkout',
    otherTax: 0,
    paymentMethodCode: selectedPaymentMethod?.code ?? '',
    paymentMethodName: selectedPaymentMethod?.title ?? '',
    payments: [
      /* By now the Checkout drop-in is only supporting one payment method */
      {
        paymentMethodCode: selectedPaymentMethod?.code ?? '',
        paymentMethodName: selectedPaymentMethod?.title ?? '',
        total: prices?.grand_total?.value ?? 0,
      },
    ],
    salesTax: calculateTaxes(prices?.applied_taxes ?? []),
    subtotalExcludingTax: prices?.subtotal_excluding_tax?.value ?? 0,
    subtotalIncludingTax: prices?.subtotal_including_tax?.value ?? 0,
  };

  if (hasAddresses) {
    const firstAddress = shippingAddresses[0];
    const selectedShippingMethod = firstAddress?.selectedShippingMethod;

    if (selectedShippingMethod) {
      orderContext.shipping = buildShippingMethod(selectedShippingMethod);
    }
  }

  return orderContext;
}

/**
 * This function sets both ShoppingCart and Order contexts to the ACDL before
 * firing the "place-order" event.
 * This code has been extracted from the ContextManager class (included into
 * the Adobe Commerce Events SDK package) and adapted in order to use directly
 * the ACDL own API methods.
 *
 * Code based on:
 *  ContextManager.ts::setShoppingCart(context: ShoppingCart): void
 *  ContextManager.ts::setOrder(context: Order): void
 *
 * @param {string} orderId The order ID generated when placed the order.
 * @returns {void}
 *
 * ContextManager class @see https://github.com/adobe/commerce-events/blob/main/packages/storefront-events-sdk/src/ContextManager.ts
 */
function setContext(orderId: string): void {
  const { data } = cartSignal.value;

  const shoppingCartContext = buildShoppingCartContext(data!);
  const orderContext = buildOrderContext(orderId, data!);

  window.adobeDataLayer = window.adobeDataLayer || [];

  // First of all it deletes the previously added data layer context entries
  window.adobeDataLayer.push(
    { shoppingCartContext: null },
    { orderContext: null }
  );

  // and finally it adds the specific data layer context entries for that request
  window.adobeDataLayer.push({ shoppingCartContext }, { orderContext });
}

/**
 * This function publishes the "place-order" event pushing it to the ACDL (a
 * plain JavaScript Array that serves as a queue).
 * This code has been extracted from the PublishManager class (included into
 * the Adobe Commerce Events SDK package) and adapted in order to use directly
 * the ACDL own API methods.
 *
 * Code based on:
 *  PublishManager.ts::placeOrder(context?: CustomContext): void
 *
 * @returns {void}
 *
 * PublishManager class @see https://github.com/adobe/commerce-events/blob/main/packages/storefront-events-sdk/src/PublishManager.ts
 */
function publishPlaceOrderEvent(): void {
  window.adobeDataLayer = window.adobeDataLayer || [];

  /*global AdobeClientDataLayer */
  /*eslint no-undef: "error"*/
  window.adobeDataLayer.push((acdl: AdobeClientDataLayer) => {
    const state = acdl.getState() || {};

    acdl.push({
      event: 'place-order',
      eventInfo: {
        ...state,
      },
    });
  });
}

/**
 * "completeCheckout" is a Commerce event available when it's installed the
 * Data Connection extension. The data this event collects is sent to the Adobe
 * Experience Platform edge.
 *
 * This is a Storefront event that collects anonymized behavioral data from the
 * shoppers as they browse the site. The data this event collects can be used
 * to create promotions and campaigns targeted to a specific set of shoppers.
 * Storefront event data includes simple and configurable products only.
 *
 * "completeCheckout" is triggered when the shopper places an order.
 *
 * The code access directly the Adobe Client Data Layer (ACDL) package instead
 * of using the Adobe Commerce Events SDK package, because the ACDL reduces the
 * effort to instrument websites by providing a standardized method to expose
 * and access any kind of data for any script. It consists of a JavaScript
 * client-side event-driven data store that can be used on web pages:
 *
 * - to collect data about what the visitors experience on the web page
 * - to communicate this data to digital analytics and reporting servers
 *
 * @param {string} orderId The order ID generated when placed the order.
 * @returns {void}
 *
 * Data Connection Events documentation @see https://experienceleague.adobe.com/docs/commerce-merchant-services/data-connection/event-forwarding/events.html?lang=en#completecheckout
 * Adobe Commerce Events SDK GitHub repo @see https://github.com/adobe/commerce-events
 * Adobe Client Data Layer documentation @see https://github.com/adobe/adobe-client-data-layer/wiki
 * Adobe Client Data Layer GitHub repo @see https://github.com/adobe/adobe-client-data-layer
 */
export function completeCheckout(orderId: string): void {
  setContext(orderId);
  publishPlaceOrderEvent();
}
