export const CART_STOCK_STATUS_FRAGMENT = /* GraphQL */ `
  fragment CartStockStatus on Cart {
    id
    items {
      __typename
      uid
      product {
        uid
        name
        sku
        stock_status
        only_x_left_in_stock
        ... on ConfigurableProduct {
          variants {
            attributes {
              uid
            }
            product {
              uid
              stock_status
            }
          }
        }
        ... on BundleProduct {
          items {
            uid
            options {
              uid
              product {
                uid
              }
            }
          }
        }
      }
      quantity
      ... on ConfigurableCartItem {
        configurable_options {
          configurable_product_option_uid
          option_label
          configurable_product_option_value_uid
          value_label
        }
      }
      ... on BundleCartItem {
        bundle_options {
          uid
          values {
            uid
          }
        }
      }
    }
  }
`;

export const getCartStockStatusQuery = /* GraphQL */ `
  query getCartStockStatus($cartId: String!) {
    cart(cart_id: $cartId) {
      ...CartStockStatus
    }
  }
  ${CART_STOCK_STATUS_FRAGMENT}
`;

export const getCustomerCartStockStatusQuery = /* GraphQL */ `
  query getCustomerCartStockStatus {
    cart: customerCart {
      ...CartStockStatus
    }
  }
  ${CART_STOCK_STATUS_FRAGMENT}
`;
