import { GetCustomerQuery } from '@/checkout/__generated__/types';
import {
  Customer as CustomerModel,
  CustomerAddress as CustomerAddressModel,
  CustomAttribute as CustomAttributeModel,
} from '@/checkout/data/models';

type Customer = GetCustomerQuery['customer'];
type NonNullableCustomer = NonNullable<Customer>;
type CustomAttributesV2 = CustomerAddress['custom_attributesV2'];
type CustomerAddresses = NonNullableCustomer['addresses'];
type CustomerAddress = NonNullable<NonNullable<CustomerAddresses>[number]>;

const isValidCustomAttribute = (attribute: any): boolean => {
  if (!attribute) return false;
  return 'code' in attribute && 'value' in attribute;
};

export const transformCustomAttributesV2 = (
  data: CustomAttributesV2
): CustomAttributeModel[] => {
  return data.filter(isValidCustomAttribute).map((attribute) => {
    const { code, value } = attribute as { code: string; value: string };
    return { code, value };
  });
};

const transformCustomerAddress = (
  data: CustomerAddress
): CustomerAddressModel => {
  const street = data.street!.filter(Boolean) as string[];
  const regionId = data.region?.region_id;
  const hasRegionId = regionId && regionId > 0;

  return {
    id: String(data.id),
    firstName: data.firstname!,
    lastName: data.lastname!,
    company: data.company || undefined,
    city: data.city!,
    street,
    postCode: data.postcode || undefined,
    vatId: data.vat_id || undefined,
    telephone: data.telephone || undefined,
    region: {
      id: hasRegionId? regionId : undefined,
      code: data.region?.region_code!,
      name: data.region?.region!,
    },
    country: {
      value: data.country_code!,
      label: data.country_code!,
    },
    customAttributes: transformCustomAttributesV2(data.custom_attributesV2),
  };
};

const transformCustomerAddresses = (
  data: CustomerAddresses
): CustomerAddressModel[] => {
  if (!data) return [];

  const customerAddresses = data.filter(Boolean) as CustomerAddress[];
  return customerAddresses.map(transformCustomerAddress);
};

const transformDefaultAddress = (
  type: 'default_billing' | 'default_shipping',
  data: CustomerAddresses
): CustomerAddressModel | undefined => {
  if (!data) return;

  const address = data.find((address) => address?.[type] === true);

  if (!address) return;

  return transformCustomerAddress(address);
};

const transformCustomer = (data: Customer): CustomerModel | undefined => {
  if (!data) return;

  const addresses = data.addresses;

  return {
    firstName: data.firstname || '',
    lastName: data.lastname || '',
    email: data.email || '',
    addresses: transformCustomerAddresses(addresses),
    defaultBillingAddress: transformDefaultAddress(
      'default_billing',
      addresses
    ),
    defaultShippingAddress: transformDefaultAddress(
      'default_shipping',
      addresses
    ),
  };
};

export { transformCustomer };
