import {
  getCartQuery,
  getCustomerCartQuery,
} from '@/checkout/api/getCart/graphql';
import { dispatchApiCall } from '@/checkout/api/utils/dispatchApiCall';
import { transformCart } from '@/checkout/data/transforms/transform-cart';

export const getCart = async (cartId?: string) => {
  return await dispatchApiCall({
    type: 'query',
    query: cartId ? getCartQuery : getCustomerCartQuery,
    options: {
      method: 'POST',
      cache: 'no-cache',
      ...(cartId ? { variables: { cartId } } : {}),
    },
    path: 'cart',
    signalType: 'cart',
    transformer: transformCart,
  });
};
