import {
  BundleCartItem,
  BundleProduct,
  Cart,
  ConfigurableCartItem,
  ConfigurableProduct,
  DownloadableCartItem,
  GiftCardCartItem,
  ProductInterface,
  ProductStockStatus,
  SimpleCartItem,
  VirtualCartItem,
} from '@/checkout/__generated__/types';
import { fetchGraphQl } from '@/checkout/api';
import { FetchError } from '@/checkout/api/errors';
import { handleNetworkError } from '@/checkout/lib/network-error';
import {
  getCartStockStatusQuery,
  getCustomerCartStockStatusQuery,
} from '@/checkout/api/getStockStatus/graphql/getStockStatus.graphql';

interface StockChecker {
  (item: CartItem): boolean;
}

type StockStatusOutput = {
  cart: Pick<Cart, 'id' | 'items'>;
};

type CartItem =
  | BundleCartItem
  | ConfigurableCartItem
  | DownloadableCartItem
  | GiftCardCartItem
  | SimpleCartItem
  | VirtualCartItem;

const isProductInStock = (product: ProductInterface): boolean => {
  return product.stock_status === ProductStockStatus.InStock;
};

const simpleStockChecker: StockChecker = (item) => {
  return isProductInStock(item.product);
};

const configStockChecker: StockChecker = (item) => {
  const { configurable_options: options } = item as ConfigurableCartItem;
  const { variants } = item.product as ConfigurableProduct;

  if (!variants || !options) return false;

  const selectedOptions = options.map(
    (option) => option?.configurable_product_option_value_uid
  );

  const selectedVariant = variants.find((variant) => {
    if (!variant || !variant.attributes) return false;

    return variant.attributes.every(
      (attribute) => attribute && selectedOptions.includes(attribute.uid)
    );
  });

  // if no matching variant is found, we assume the product is out of stock
  if (!selectedVariant) return false;

  const { product: variantProduct } = selectedVariant;

  if (!variantProduct) return false;

  return isProductInStock(variantProduct);
};

const bundleStockChecker: StockChecker = (item) => {
  const { product } = item as BundleCartItem;

  if (!isProductInStock(product)) return false;

  // if any of the items inside the bundle is ouf of stock, it will not be part of this list
  const { bundle_options: options } = item as BundleCartItem;
  const { items: bundle } = product as BundleProduct;

  return bundle?.length === options?.length;
};

const getStockChecker = (type?: string): StockChecker => {
  switch (type) {
    case 'ConfigurableCartItem':
      return configStockChecker;
    case 'BundleCartItem':
      return bundleStockChecker;
    default:
      return simpleStockChecker;
  }
};

const isOutOfStock = (item: CartItem) => {
  const checker = getStockChecker(item.__typename);
  return checker(item) === false;
};

export const getStockStatus = async (
  cartId?: string
): Promise<ProductStockStatus> => {
  const { data, errors } = await fetchGraphQl<StockStatusOutput>(
    cartId ? getCartStockStatusQuery : getCustomerCartStockStatusQuery,
    {
      method: 'GET',
      cache: 'no-cache',
      ...(cartId ? { variables: { cartId } } : {}),
    }
  ).catch(handleNetworkError);

  if (errors) throw new FetchError(errors);

  const cartItems = data?.cart?.items;

  if (!cartItems || !cartItems.length) return ProductStockStatus.InStock;

  const outOfStock = cartItems.filter((item) => {
    if (!item) return false;
    return isOutOfStock(item as CartItem);
  });

  return outOfStock.length > 0
    ? ProductStockStatus.OutOfStock
    : ProductStockStatus.InStock;
};
