export const getCustomerQuery = /* GraphQL */ `
  query getCustomer {
    customer {
      firstname
      lastname
      email
      addresses {
        id
        default_shipping
        default_billing
        city
        country_code
        firstname
        lastname
        company
        postcode
        vat_id
        region {
          region
          region_id
          region_code
        }
        street
        telephone
        custom_attributesV2 {
          ... on AttributeValue {
            code
            value
          }
        }
      }
    }
  }
`;
