import { PlaceOrderMutation } from '@/checkout/__generated__/types';
import {
  FetchError,
  MissingCart,
  UnexpectedError,
} from '@/checkout/api/errors';
import { fetchGraphQl } from '@/checkout/api/fetch-graphql';
import { placeOrderMutation } from '@/checkout/api/placeOrder/graphql';
import { handleNetworkError } from '@/checkout/lib/network-error';
import { completeCheckout } from '@/checkout/utils/storefrontEvents';
import { events } from '@adobe/event-bus';
import { FetchQueryError } from '@adobe/fetch-graphql';

function handleErrors(errors: FetchQueryError) {
  const isFetchError = errors.every((error) => error.extensions?.category);

  if (isFetchError) throw new FetchError(errors);

  throw new UnexpectedError(errors[0].message);
}

export const placeOrder = async (cartId: string) => {
  if (!cartId) throw new MissingCart();

  const { data, errors } = await fetchGraphQl<PlaceOrderMutation>(
    placeOrderMutation,
    {
      variables: { cartId },
    }
  ).catch(handleNetworkError)

  if (errors) handleErrors(errors);

  const order = data.placeOrder!.orderV2;

  completeCheckout(order!.number);

  // Emit the order to the checkout/order event
  events.emit('checkout/order', order); 
  // Reset the cart after the order is placed
  events.emit('cart/reset', undefined); 
};
